






















































































































































.form {
    .li {
        display: flex;

        /deep/ .el-select {
            width: 250px;
        }

        /deep/ .el-textarea__inner {
            width: 250px;
        }

        .item {
            width: 100%;
            display: flex;
            line-height: 32px;
            height: 32;

            .important {
                color: red;
            }

            .label {
                font-family: "PFC";
                min-width: 0.8rem;
                margin-right: 2px;
                color: #333;
                text-align: right;
            }

            .box {
                width: 100%;
                border-radius: 4px;

                padding: 0 10px;
                box-sizing: border-box;
            }

            .rt {
                flex: 1;
            }

            .btns {
                width: 100%;
                display: flex;
                justify-content: flex-end;

            }
        }
    }
}
