














































































































































.page {
    display: flex;
    justify-content: flex-end;
}

.cus-btn {
    color: #2E4AF7;
    font-size: 14px;
    padding-left: 25px;
    cursor: pointer;
    line-height: 22px;
    position: relative;

    &:first-child {
        padding-left: 0;
    }
}

.table-btns {
    display: flex;
    justify-content: left;

    &.three {
        justify-content: space-between;

        .cus-btn {
            padding: 0;
        }

        .line:after {
            content: "";
            position: absolute;
            display: block;
            right: -13px;
            top: 6px;
            bottom: 0;
            height: 12px;
            width: 1px;
            background: #E3E5EC;
        }
    }


    .center {
        width: 100%;
    }

}


/deep/ .active-row {
    background-color: #F5F7FA;
}
