




















































































































































































































































































































































































































































































































































































































.form {
    .li {
        display: flex;
        margin-bottom: 22px;



        .item {
            width: 100%;
            display: flex;
            line-height: 32px;
            height: 32;

            .label {
                font-family: "PFC";
                min-width: 0.8rem;
                margin-right: 2px;
                color: #333;
                text-align: right;
            }

            .logo-img {
                width: 95px;
                height: 95px;
                line-height: 0;
                padding: 30px;
                box-sizing: border-box;
            }

            .box {

                width: 100%;
                border-radius: 4px;
                color: #666;

                box-sizing: border-box;
            }

            .rt {
                flex: 1;
                padding: 0 10px;
            }

            .btns {
                width: 100%;
                display: flex;
                justify-content: flex-end;

            }

            .fileBtn {
                font-size: 14px;
                color: #2E4AF7;
                text-decoration: underline;
            }
        }
    }

    /deep/ .hide .el-upload--picture-card {
        display: none;
    }
}
