











































































































































































































































































































































.form {
    .li {
        /deep/ .el-form-item {
            width: 100%;
        }

        .notice {
            position: absolute;
            right: 0;
        }

        .red {
            color: orange;
            font-size: 12px;
        }

        .success {
            color: green;
        }

        display: flex;

        .item {
            .unit {
                margin: 0 0.15rem;
            }

            width: 100%;
            display: flex;
            line-height: 32px;
            height: 32;

            .label {
                font-family: "PFC";
                min-width: 1rem;
                margin-right: 2px;
                color: #333;
                text-align: right;
            }

            .box {
                width: 100%;
                border-radius: 4px;
                color: #666;

                box-sizing: border-box;
            }

            .rt {
                flex: 1;
            }

            .btns {
                width: 100%;
                display: flex;
                justify-content: flex-end;

            }
        }
    }
}
