

































































































.page {
    display: flex;
    justify-content: flex-end;
}

.cus-btn {
    color: #2E4AF7;
    font-size: 14px;
    padding: 0 2px;
    cursor: pointer;
    line-height: 22px;
    position: relative;
}

.excep-theme {
    color: red;
}

/deep/ .el-table .warning-row {
    background: rgb(255, 243, 243);
}

.table-btns {
    display: flex;
    justify-content: space-between;

    .center {
        width: 100%;
        display: flex;
        justify-content: center;
    }

    .line:after {
        content: "";
        position: absolute;
        display: block;
        right: -12px;
        top: 6px;
        bottom: 0;
        height: 12px;
        width: 1px;
        background: #E3E5EC;
    }
}

/deep/ .active-row {
    background-color: #F5F7FA;
}
