



































































































.step {
    margin-bottom: 40px;
}

.content-title {
    font-family: 'PFC';
    color: #333333;
    font-size: 14px;
    line-height: 25px;
    font-weight: 600;
}

.log-content {


    .head {
        display: flex;

        .log-label {
            color: #666666;
            font-size: 12px;
            line-height: 22px;
            margin-right: 60px;
        }
    }

    .log-ul {
        margin-top: 30px;
        list-style-type: none;
        color: #666666;
        font-size: 12px;
        max-height: 300px;
        overflow-y: auto;

        .log-li {
            .lt {
                max-width: 500px;
            }

            list-style-type: none;
            line-height: 40px;
            border-bottom: 1px solid #E4E4E4;
            margin-bottom: 8px;
            display: flex;
            justify-content: space-between;

            .rt {
                padding-right: 20px;
            }
        }
    }
}
