







































































































































































































































































.form {
    .li {
        &:last-child {
            margin-bottom: 0;
        }

        /deep/ .el-select {
            width: 100%;
        }

        display: flex;
        margin-bottom: 22px;

        .item {
            width: 100%;
            display: flex;
            line-height: 32px;
            height: 32;

            .label {
                font-family: "PFC";
                min-width: 0.5rem;
                margin-right: 2px;
                color: #333;
                text-align: right;
            }

            .box {
                width: 100%;
                border-radius: 4px;
                color: #666;
                padding: 0 10px;
                box-sizing: border-box;
            }

            .rt {
                flex: 1;

                /deep/ .el-form-item {
                    margin-bottom: 0
                }
            }

            .btns {
                width: 100%;
                display: flex;
                justify-content: flex-end;

            }
        }
    }

    /deep/ .hide .el-upload--picture-card {
        display: none;
    }
}
