






























































































































































































































































































































































































































































































































































































































































































































































































.form {
    .li {
        &:last-child {
            margin-bottom: 0;
        }

        .link {
            cursor: pointer;
            color: #0628f7;
            text-decoration: underline;
        }

        .box {
            &.contract {
                padding: 16px 10px 16px 22px;
                background-color: rgb(230, 234, 254);
            }

            .li {
                display: flex;
                justify-content: space-between;
                margin-bottom: 16px;

                &:last-child {
                    margin-bottom: 0;
                }

                label {
                    font-weight: 700;
                }

                span {
                    color: #666;
                }

                .lt {
                    width: 60%;
                }

                .rt {
                    width: 40%;
                }
            }
        }

        /deep/ .el-form-item {
            margin-bottom: 0;
            width: 100%;
        }

        /deep/ .el-input__inner {
            // width: 2.2rem;
        }

        .notice {
            position: absolute;
            right: 0;
        }

        .red {
            color: orange;
            font-size: 12px;
        }

        .success {
            color: green;
        }

        .FPfile {
            /deep/ .el-upload {
                text-align: center;
                display: inline-block;
            }
        }

        /deep/ .el-upload {
            text-align: left;
            display: flex;
        }

        /deep/ .hide .el-upload--picture-card {
            display: none;
        }

        .fileName {
            white-space: nowrap;
            width: 320px;
            text-overflow: ellipsis;
            height: 32px;
            overflow: hidden;
        }

        display: flex;
        margin-bottom: 22px;

        .item {
            .unit {
                margin: 0 0.15rem 0 0;
            }

            width: 100%;
            display: flex;
            line-height: 32px;
            height: 32;

            .label {
                font-family: "PFC";
                min-width: 1rem;
                margin-right: 2px;
                color: #333;
                text-align: right;
            }

            .box {
                width: 100%;
                border-radius: 4px;
                color: #666;
                box-sizing: border-box;
            }

            .rt {
                flex: 1;
            }

            .btns {
                width: 100%;
                display: flex;
                justify-content: flex-end;

            }
        }
    }
}
